/* @media (width: 50em){

} */

@media (max-width:50em) {
    .navbar {
        display: flex;
    }
    .list__items {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        position: fixed;
        inset: 0 0 0 0%;

        grid-gap: 1.5rem;

        gap: 1.5rem;
        list-style: none;        

        background-color: var(--img-back);
        border: 1px solid var(--nav-border);

        transform: translateY(100%);
        transition: 400ms;
    }

    .list__items.active {
        transform: translateX(0);
        transition: 450ms;
    }

    .menu_icon {
        position: absolute;
        right: 2rem;
        top: 1.5rem;
        color: var(--main-black);
        cursor: pointer;
    }

    .menu_icon .menu {
        font-size: 40px;
    }
    .close_icon {
        color: var(--main-black);
        cursor: pointer;
        position: fixed;
        top: 2rem;
        right: 2rem;
    }
}
.ideas {
  width: 100%;
